import React,{Component} from 'react'
import Index from './Index.jsx'


class Home extends Component{
	constructor(){
		super()
		this.state={route:'Index'}
	}

	change=()=>{
		this.setState({route:'home'})
	}

	render(){
		return(
			<div>
			{
				(this.state.route==='home')
				?<div>Home</div>
				:(
					<div>
						<Index click={this.change}/>
					</div>
					)
			}
		</div>

			)
	}
}
export default Home;


